.w-25 {
  width: 25% !important;
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.w-33 {
  width: 33% !important;
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.w-50 {
  width: 50% !important;
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.w-66 {
  width: 66% !important;
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.w-75 {
  width: 75% !important;
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.w-100 {
  width: 100% !important;
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.text-white {
  color: #fff !important;
  margin-left: 0;
  margin-right: 0;
}
.text-white a {
  color: inherit !important;
}

.m-auto {
  margin-left: auto;
  margin-right: auto;
}

.my-1 {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.my-2 {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.mt-1 {
  margin-top: 1rem;
}

.mb-1 {
  margin-bottom: 1rem;
}

.mr-1 {
  margin-right: 1rem;
}

.ml-1 {
  margin-left: 1rem;
}

.btn {
  background-color: #fff;
  color: #636467;
  padding: 0.125rem 0.55rem;
  font-weight: bold;
  border: 1px solid #d40404;
}
.btn:hover, .btn.active {
  background-color: #d40404;
  color: #fff;
}

.btn-wrapper .btn,
.btn-wrapper span {
  margin-right: 0.25rem;
  display: inline-flex;
  margin-bottom: 0.5rem;
}
@media screen and (max-width: 760px) {
  .btn-wrapper .btn,
.btn-wrapper span {
    min-width: 20%;
  }
}
.btn-wrapper span {
  padding-top: 0.125rem;
  margin-left: 0.5rem;
}
.btn-wrapper span:first-of-type {
  margin-left: 0;
}

.tab-group {
  border-bottom: 1px solid #ccc;
}
.tab-group .tab-bar-item {
  margin-right: 0.5rem;
  border-radius: 8px 8px 0 0;
  border: none;
  padding: 0.35rem 0.75rem;
}

.d-none {
  display: none;
}

.d-flex {
  display: flex;
}

.d-block {
  display: block;
}

.fade {
  opacity: 0.25;
}

.show {
  display: flex;
}

.hide {
  display: none !important;
}

.flex-wrap {
  flex-wrap: wrap;
}

h1 {
  color: #000 !important;
}

h5,
h6 {
  color: #000;
}

.text-sm {
  font-size: 0.75rem;
  font-weight: bold;
  color: #666;
}

.text-normal {
  font-size: 1rem;
}

.icon-count {
  align-self: center;
  margin-left: 0.25rem;
  background: #fff;
  padding: 0.25rem 0;
  border-radius: 100%;
  color: #d40404;
  border: 1px dashed #d40404;
  width: 2rem;
  text-align: center;
}

p {
  margin-bottom: 0.5rem;
}
p.has-medium-font-size {
  margin-bottom: 0.5rem;
}

.wp-block-group.wrapper {
  margin-top: 150px;
}
.wp-block-group.wrapper .wp-block-group__inner-container {
  margin-top: -150px;
}

.wp-block-getwid-custom-post-type.has-layout-grid .wp-block-getwid-custom-post-type__wrapper {
  margin-left: -1rem;
  margin-right: -1rem;
}
.wp-block-getwid-custom-post-type.has-layout-grid .wp-block-getwid-custom-post-type__wrapper .wp-block-getwid-custom-post-type__post {
  display: flex;
  flex-direction: column;
  min-height: 200px;
  padding-left: 1rem;
  padding-right: 1rem;
}
.wp-block-getwid-custom-post-type.has-layout-grid .wp-block-getwid-custom-post-type__wrapper .wp-block-getwid-template-post-title {
  color: #00b2df !important;
  background: #fff;
  margin-top: 0;
  margin-bottom: 0;
  padding: 1rem;
}
.wp-block-getwid-custom-post-type.has-layout-grid .wp-block-getwid-custom-post-type__wrapper .wp-block-getwid-template-post-title A {
  color: inherit !important;
}
.wp-block-getwid-custom-post-type.has-layout-grid .wp-block-getwid-custom-post-type__wrapper .wp-block-getwid-template-post-title A:hover {
  color: #92b751 !important;
}
.wp-block-getwid-custom-post-type.has-layout-grid .wp-block-getwid-custom-post-type__wrapper .wp-block-getwid-template-post-content {
  background: #fff;
  margin: 0;
  padding: 0 1rem 1rem 1rem;
  flex: 1;
}
.wp-block-getwid-custom-post-type.has-layout-grid .wp-block-getwid-custom-post-type__wrapper .wp-block-getwid-template-post-featured-image {
  margin-bottom: 0;
}